#root {
  padding: 0;
  margin: 0;

  top: 0;
  left: 0;

  width: 100%;
  min-height: 650px;
  background: #ffffff;
}
h3 {
  margin: 0;
  color: #ffffff;
}
button {
  background: none;
  border: none;

}
.header {
  padding: 15px;
  background: #56ccf2;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.287);
}
.main {
  min-height: 600px;
  display: flex;
}
a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold; 
}
.app-loading-link {
  margin: auto;
  height: 30px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 4px;
  background: #391fa4;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.287);
}
.people-search-engine {
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  min-width: 300px;
}
.list-container {
  width: 100%;
}
.item-container {
  height: 40px;
  width: calc(80% - 50px);
  min-width: 250px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5px;
  border-style: solid;
  border-color: #cacacad0;
  border-width: 1px;
  border-radius: 4px;

  font-weight: bold;
  display: flex;
  align-items: center;
  padding-left: 10px;
  padding-right: 5px; 
}
.name {
  margin-left: auto;
}
.arrow {
  margin-left: auto; 
  width: 30px;
  transition: 0.5s;
}
.arrow-rotate {
  transform: rotate(90deg);
  transition: 0.5s;
}
.search-line {
  height: 45px;
  width: calc(100% - 50px);
  margin: 10px;
  padding-left: 20px; 
  padding-right: 20px;
  font-size: 20px;
  border-radius: 4px;
  border-width: 1px;
}
.person-info {
  margin-left: auto;
  margin-right: auto;
  width: calc(80% - 35px);
  min-width: 265px;
  height: 0px;
  opacity: 0;
  border-style: solid;
  border-color: #cacacad0;
  border-width: 1px;
  border-radius: 4px;
  transition: 0.5s;
}
.person-info-open {
  height: 80px;
  opacity: 1;
  transition: 0.5s;
}
.navigator {
  display: flex; 
  padding: 15px;
  justify-content: center;
}
.square-navigator {
  border-style: solid;
  border-color: #cacacad0;
  border-width: 1px;
  border-radius: 4px;
  
  width: 30px;
  height: 30px;
  margin: 5px;
  
  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: bold;
  color: #cacacad0;
  background: #ffffff;
}
.square-navigator:hover {
  color: #ffffff;
  background: #cacacad0;
}
.square-navigator-center {
  border-style: solid;
  border-color: #cacacad0;
  border-width: 1px;
  border-radius: 4px;
  
  width: 30px;
  height: 30px;
  margin: 5px;
  
  display: flex;
  align-items: center;
  justify-content: center;

  font-weight: bold;
  color: #ffffff;
  background: #cacacad0;
}
.left-arrow {
  transform: rotate(90deg);
}
.right-arrow {
  transform: rotate(270deg);
}
.loading-screen {
  position: fixed;
  width: 200px;
  height: 200px;
  background: rgba(0, 0, 0, 0.678);
  color: #ffffff;
  font-weight: bold;
  font-size: 30px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}